:root {

  --font-primary: 'Open Sans', sans-serif;

  --color-primary: rgba(33, 49, 100, 0.9);
  --color-black: black;
  --color-gray: #18181a;
  --color-white: white;
  --color-green: rgb(183, 228, 183);
  --bg-color:black;
  --text-color:white;
  --color-secondary: rgb(24, 24, 26);
  --color-default-gradient: linear-gradient(0deg, rgba(33, 49, 100, 0.9), white);
}

body {
  margin: 0;
  padding: 0%;
 
  width: 100%;
  height: 100%;
  color: black;
  color: var(--color-black);
}

