}

.navbar-menu a.navbar-menu-item-login:hover {
  color: white;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  padding: 2px;
}

/* css for the custom left line in the menu */
.navbar-menu a:before {
  position: absolute;
  width: 2px;
  height: 100%;
  left: 0px;
  top: 0px;
  content: '';
  background: var(--text-color);
  opacity: 0.3;
  transition: all 0.3s;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.switch {
  position: relative;
